<script>
export default {
  name: 'Interview',
};
</script>

<script setup>
import Constants from '../utils/constants';
import { storeToRefs } from 'pinia';
import { useNavStore } from '../stores/nav';
import InterviewItem from '../components/interview/InterviewItem';
import { useResultStore } from '../stores/result';
import { computed, onMounted, ref, watch } from 'vue';
import InterviewButtonsList from '../components/interview/InterviewButtonsList';
import { useInterviewStore } from '../stores/interview';

const navStore = useNavStore();
const { view } = storeToRefs(navStore);

const interviewStore = useInterviewStore();
const { percent } = storeToRefs(interviewStore);

const resultStore = useResultStore();
const {
  vertical,
  arrRevenueScale,
  gtmMotion,
  software,
  showSoftware,
} = storeToRefs(resultStore);

const { cleanValues } = resultStore;

const interviewLock = ref(false);
const disableAll = ref(false);

const step = computed(() => {
  let result = 0;

  if (vertical.value) {
    result++;
  }

  if (gtmMotion.value) {
    result++;
  }

  if (showSoftware.value && software.value) {
    result++;
  }

  if (arrRevenueScale.value) {
    result++;
  }

  return result;
});

const steps = computed(() => {
  let array = [];

  array.push({
    before: 'I am an',
    after: 'company...',
    model: vertical,
    options: Constants.RESULT.VERTICAL,
    cleanValues: ['vertical', 'gtm-motion', 'software', 'arr-revenue-scale'],
    isPending: true,
    hideEdit: true,
  });

  array.push({
    before: 'with a',
    after: 'sales motion...',
    model: gtmMotion,
    options: Constants.RESULT.GTM_MOTION,
    cleanValues: ['gtm-motion', 'software', 'arr-revenue-scale'],
  });

  if (showSoftware.value) {
    array.push({
      before: 'selling',
      after: 'software...',
      model: software,
      options: Constants.RESULT.SOFTWARE,
      cleanValues: ['software', 'arr-revenue-scale'],
    });
  }

  array.push({
    before: 'operating at',
    after: '...',
    model: arrRevenueScale,
    options: Constants.RESULT.ARR__REVENUE_SCALE,
    cleanValues: ['arr-revenue-scale'],
    noSpace: true,
  });

  return array;
});

const currentStep = computed(() => {
  if (step.value >= steps.value.length)
    return steps.value[steps.value.length - 1];

  return steps.value[step.value];
});

const previousStep = computed(() => {
  if (step.value <= 0) return null;

  return steps.value[step.value - 1];
});

function selectOption(option) {
  currentStep.value.model.value = option;
}

watch(
  () => step.value,
  () => {
    checkStep();
  }
);

let dirty = false;
function checkStep() {
  percent.value = Math.round((step.value / steps.value.length) * 100);

  if (currentStep.value && previousStep.value && previousStep.value.isPending && !dirty) {
    interviewLock.value = true;
    dirty = true;

    setTimeout(() => {
      interviewLock.value = false;
    }, 2000);
  } else {
    interviewLock.value = false;
  }

  if (step.value >= steps.value.length) {
    disableAll.value = true;

    setTimeout(() => {
      view.value = Constants.NAV.VIEW.CALCULATING;
    }, 2000);
  }
}

function skip() {
  view.value = Constants.NAV.VIEW.RESULTS;
}

const computedSteps = computed(() => {
  return steps.value.filter((item, index) => {
    return index <= step.value && (!interviewLock.value || item.isPending);
  });
});

function makeBeautiful() {
  if (currentStep.value.isPending) {
    const firstButton = document.querySelector(
        '.growth-metrics .btns-list .button'
    );

    setTimeout(() => {
      firstButton.classList.add('hover');

      setTimeout(() => {
        firstButton.classList.remove('hover');
        firstButton.click();
      }, 1000);
    }, 1000);
  }
}

onMounted(() => {
  checkStep();
  makeBeautiful();
});
</script>

<template>
  <section class="growth-metrics">
    <div class="gm-content-area">
      <div class="container">
        <div class="interview-items">
          <transition-group name="interview-items" mode="out-in">
            <interview-item
              v-for="(item, index) in computedSteps"
              :key="item.model"
              :item="item"
              :index="index"
              :step="step"
              :lock="interviewLock || disableAll"
              @edit="cleanValues"
            />
          </transition-group>

          <interview-buttons-list
            v-if="currentStep"
            :options="currentStep.options"
            :lock="interviewLock || disableAll"
            @select="selectOption"
          />
        </div>
      </div>
    </div>

    <a href="#" class="skip" @click.prevent="skip">Skip</a>
  </section>
</template>

<style>
.interview-items-enter-active,
.interview-items-leave-active {
  transition: all 0.5s ease;
}

.interview-items-enter-from,
.interview-items-leave-to {
  opacity: 0;
  transform: scale(0.01);
}

.interview-items-leave-active {
  position: absolute;
}
</style>
