import { storeToRefs } from 'pinia';
import { computed } from 'vue';
import { useResultStore } from '../stores/result';
import Constants from '../utils/constants';

export default function useChart(results) {
  const CHAR_TYPE_BAR = 'bar';
  const CHAR_TYPE_INLINE = 'inline';

  const resultStore = useResultStore();
  const {
    vertical,
    arrRevenueScale,
    gtmMotion,
    software,
    showSoftware,
  } = storeToRefs(resultStore);

  const computedResult = computed(() =>
    results.find(
      (result) =>
        result.vertical === vertical.value &&
        result.gtm_motion === gtmMotion.value &&
        (result.software === software.value || ! showSoftware.value) &&
        result.arr__revenue_scale === arrRevenueScale.value
    )
  );

  const computedMax = computed(() => {
    if (! computedResult.value) {
      return 0;
    }

    return Math.max(
      computedResult.value[Constants.RESULT.PERCENTILE.TH25.value],
      computedResult.value[Constants.RESULT.PERCENTILE.TH50.value],
      computedResult.value[Constants.RESULT.PERCENTILE.TH75.value],
      computedResult.value[Constants.RESULT.PERCENTILE.TH90.value]
    );
  });

  const computedMin = computed(() => {
    if (! computedResult.value) {
      return 0;
    }

    return Math.min(
      computedResult.value[Constants.RESULT.PERCENTILE.TH25.value],
      computedResult.value[Constants.RESULT.PERCENTILE.TH50.value],
      computedResult.value[Constants.RESULT.PERCENTILE.TH75.value],
      computedResult.value[Constants.RESULT.PERCENTILE.TH90.value]
    );
  });

  const computedTrailing = computed(() => {
    if (! computedResult.value) {
      return 0;
    }

    return computedResult.value[Constants.RESULT.PERCENTILE.TRAILING.value];
  });

  const computedWithThePack = computed(() => {
    if (! computedResult.value) {
      return 0;
    }

    return computedResult.value[
      Constants.RESULT.PERCENTILE.WITH_THE_PACK.value
    ];
  });

  const computedPullingAhead = computed(() => {
    if (! computedResult.value) {
      return 0;
    }

    return computedResult.value[
      Constants.RESULT.PERCENTILE.PULLING_AHEAD.value
    ];
  });

  const chartType = computed(() => {
    if (computedResult.value) {
      if (
        computedResult.value[Constants.RESULT.PERCENTILE.TH25.value] ||
        computedResult.value[Constants.RESULT.PERCENTILE.TH50.value] ||
        computedResult.value[Constants.RESULT.PERCENTILE.TH75.value] ||
        computedResult.value[Constants.RESULT.PERCENTILE.TH90.value]
      ) {
        return CHAR_TYPE_BAR;
      } else if (
        computedResult.value[Constants.RESULT.PERCENTILE.PULLING_AHEAD.value] ||
        computedResult.value[Constants.RESULT.PERCENTILE.TRAILING.value] ||
        computedResult.value[Constants.RESULT.PERCENTILE.WITH_THE_PACK.value]
      ) {
        return CHAR_TYPE_INLINE;
      }
    }

    return null;
  });

  function formatNumber(number, format) {
    switch (format) {
      case '%': {
        return `${number}%`;
      }

      case '#x': {
        return `${number}x`;
      }

      case '#.#x': {
        if (0 === number % 1) {
          return `${number}.0x`;
        }

        return `${number}x`;
      }

      case '$##,###': {
        return Number(number).toLocaleString('en-US', {
          style: 'currency',
          currency: 'USD',
          minimumFractionDigits: 0,
          maximumFractionDigits: 0,
        });
      }

      case '$##,###.##': {
        return Number(number).toLocaleString('en-US', {
          style: 'currency',
          currency: 'USD',
        });
      }

      case 'months': {
        return `${number} months`;
      }

      default: {
        return number;
      }
    }
  }

  return {
    CHAR_TYPE_BAR,
    CHAR_TYPE_INLINE,

    computedResult,

    computedMax,
    computedMin,

    computedTrailing,
    computedWithThePack,
    computedPullingAhead,

    chartType,

    formatNumber,
  };
}
