<script>
export default {
  name: 'ResultsArticleChartInline',
};
</script>

<script setup>
import { computed, onMounted, ref } from 'vue';
import Intersection from '../../utils/intersection';
import Constants from '../../utils/constants';
import useChart from '../../composables/useChart.js';
import { storeToRefs } from 'pinia';

const props = defineProps({
  results: {
    type: Array,
    required: true,
  },
  dataFormat: {
    type: String,
    default: '',
  },
});

const chart = useChart(props.results);
const { formatNumber } = chart;
const {
  computedTrailing,
  computedWithThePack,
  computedPullingAhead,
} = storeToRefs(chart);

const formattedTrailing = computed(() => {
  return formatNumber(computedTrailing.value, props.dataFormat);
});

const formattedWithThePack = computed(() => {
  return formatNumber(computedWithThePack.value, props.dataFormat);
});

const formattedPullingAhead = computed(() => {
  return formatNumber(computedPullingAhead.value, props.dataFormat);
});

const charsTrailingNumber = computed(() => {
  return formattedTrailing.value.length;
});

const charsWithThePackNumber = computed(() => {
  return formattedWithThePack.value.length;
});

const charsPullingAheadNumber = computed(() => {
  return formattedPullingAhead.value.length;
});

const elRef = ref(null);
const isOnScreen = ref(false);
onMounted(() => {
  const intersection = new Intersection();

  intersection.observe(elRef.value, (entry) => {
    isOnScreen.value = entry.isIntersecting;

    if (isOnScreen.value) {
      intersection.unobserve(elRef.value);
    }
  });
});
</script>

<template>
  <div class="gm-inline-chart-wrapper">
    <div
      ref="elRef"
      class="gm-inline-chart"
      :class="{ 'is-hidden': !isOnScreen }"
    >
      <span class="bar deep-ocean">
        <span class="data" :class="{ squeeze: charsTrailingNumber > 6 }">{{
          formattedTrailing
        }}</span>

        <span class="info">{{
          Constants.RESULT.PERCENTILE.TRAILING.label
        }}</span>
      </span>

      <span class="bar marina">
        <span class="data" :class="{ squeeze: charsWithThePackNumber > 6 }">{{
          formattedWithThePack
        }}</span>

        <span class="info">{{
          Constants.RESULT.PERCENTILE.WITH_THE_PACK.label
        }}</span>
      </span>

      <span class="bar avocado">
        <span class="data" :class="{ squeeze: charsPullingAheadNumber > 6 }">{{
          formattedPullingAhead
        }}</span>

        <span class="info">{{
          Constants.RESULT.PERCENTILE.PULLING_AHEAD.label
        }}</span>
      </span>
    </div>
  </div>
</template>