import { defineStore } from 'pinia';
import { computed } from 'vue';

const GROWTH_METRICS = 'growth_metrics';
const RETENTION_METRICS = 'retention_metrics';
const MARGIN_METRICS = 'margin_metrics';
const SALES_EFFICIENCY_METRICS = 'sales_efficiency_metrics';
const BUSINESS_EFFICIENCY_METRICS = 'business_efficiency_metrics';

const id = 'growth-app-metrics';

// eslint-disable-next-line import/prefer-default-export
export const useMetricsStore = defineStore(id, () => {
  const metrics = computed(() => window.resultsMetrics);

  const growthMetrics = computed(() =>
    metrics.value.filter((metric) => GROWTH_METRICS === metric.type)
  );

  const retentionMetrics = computed(() =>
    metrics.value.filter((metric) => RETENTION_METRICS === metric.type)
  );

  const marginMetrics = computed(() =>
    metrics.value.filter((metric) => MARGIN_METRICS === metric.type)
  );

  const salesEfficiencyMetrics = computed(() =>
    metrics.value.filter((metric) => SALES_EFFICIENCY_METRICS === metric.type)
  );

  const businessEfficiencyMetrics = computed(() =>
    metrics.value.filter(
      (metric) => BUSINESS_EFFICIENCY_METRICS === metric.type
    )
  );

  return {
    growthMetrics,
    retentionMetrics,
    marginMetrics,
    salesEfficiencyMetrics,
    businessEfficiencyMetrics,
  };
});
