<script>
export default {
  name: 'ResultsNote',
};
</script>

<script setup>
import { computed, onMounted } from 'vue';
import useAccordion from '../../composables/useAccordion';

const { el, toggle, init, isToggled } = useAccordion();

const title = computed(() => window.resultsNoteTitle);
const text = computed(() => window.resultsNoteText);

onMounted(() => {
  init();

  const $body = $('body');

  $('.gm-opener').click(() => {
    $body.toggleClass('gm-active');
  });

  $('.gm-overlay').click(() => {
    $body
      .removeClass('gm-active')
      .find('.gm-opener')
      .removeClass('active')
      .next()
      .slideUp(200);
  });

  $('.gm-drawer-opener').click(() => {
    const isOpen = $body.hasClass('gm-drawer-active');

    if (!isOpen) {
      window.scrollPosition = window.scrollY;
    }

    $body.toggleClass('gm-drawer-active');

    if (isOpen) {
      setTimeout(() => {
        window.scrollTo(0, window.scrollPosition);
      }, 30);
    }
  });
});
</script>

<template>
  <div class="gm-openclose">
    <div class="gm-container">
      <a ref="el" href="#" class="gm-opener" data-more @click.prevent="toggle">
        {{ title }}
      </a>

      <div class="slide" v-html="text" />
    </div>
  </div>
</template>
