<script>
import Intro from './views/Intro';
import Interview from './views/Interview';
import Results from './views/Results';

export default {
  name: 'App',
  components: {
    Intro,
    Interview,
    Results,
  },
};
</script>

<script setup>
import Constants from './utils/constants';
import { storeToRefs } from 'pinia';
import { useNavStore } from './stores/nav';
import BackgroundWrapper from './components/ui/BackgroundWrapper';
import { computed, onMounted, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import InterviewProgressBar from './components/interview/InterviewProgressBar';
import useUtm from "./composables/useUtm";

const route = useRoute();
const router = useRouter();
const utm = useUtm();

const navStore = useNavStore();
const { view, isResetting } = storeToRefs(navStore);
const { reset } = navStore;

const currentView = computed(() => {
  switch (view.value) {
    case Constants.NAV.VIEW.INTRO:
      return 'Intro';
    case Constants.NAV.VIEW.INTERVIEW:
      return 'Interview';
    default:
      return null;
  }
});

const handleKeyDown = (e) => {
  if (e.keyCode === 81 && e.ctrlKey) {
    reset();
  }
};

window.addEventListener('keydown', handleKeyDown);

const afterEnter = () => {
  document.getElementById('app').classList.add('is-result');
};

const isMounted = ref(false);
const displayBackground = ref(true);
onMounted(() => {
  setTimeout(() => {
    if (view.value === Constants.NAV.VIEW.RESULTS) {
      displayBackground.value = false;
      afterEnter();
    }

    isMounted.value = true;
    utm.sendUtm('init');
  }, 0);
});
</script>

<template>
  <template v-if="isMounted && !isResetting">
    <background-wrapper
      v-if="displayBackground"
      :class="{ hide: view === Constants.NAV.VIEW.RESULTS }"
    />

    <transition name="other-views" mode="out-in" appear>
      <component :is="currentView" />
    </transition>

    <transition name="results" mode="out-in" v-on:after-enter="afterEnter">
      <results v-if="view === Constants.NAV.VIEW.RESULTS" />
    </transition>

    <interview-progress-bar v-if="view === Constants.NAV.VIEW.INTERVIEW" />
  </template>
</template>

<style>
.other-views-enter-active {
  transition: all 0.5s 0.25s cubic-bezier(0.37, 0.01, 0, 0.98);
}

.other-views-leave-active {
  transition: all 0.5s cubic-bezier(0.37, 0.01, 0, 0.98);
}

.other-views-enter-from {
  opacity: 0;
  transform: translate3d(0, 40px, 0);
}

.other-views-leave-to {
  opacity: 0;
  transform: translate3d(0, -40px, 0);
}

.results-enter-active {
  transition: all 0.5s 0.25s cubic-bezier(0.37, 0.01, 0, 0.98);
}

.results-leave-active {
  transition: all 0.5s cubic-bezier(0.37, 0.01, 0, 0.98);
}

.results-enter-from,
.results-leave-to {
  opacity: 0;
  transform: translate3d(0, 100vh, 0);
}
</style>
