/* eslint-disable */

import { useRoute } from 'vue-router';

export default function useUtm() {
  const route = useRoute();

  const allowedParams = [
    'utm_source',
    'utm_medium',
    'utm_campaign',
    'utm_term',
    'utm_content',
    'arr-revenue-scale',
    'gtm-motion',
    'software',
    'vertical',
  ];

  const paramLabels = {
    'arr-revenue-scale': 'ARR Revenue Scale',
    'gtm-motion': 'GTM Motion',
    'software': 'Software',
    'vertical': 'Vertical',
  }

  const paramValues = {
    'top_down': 'Top Down',
    'bottom_up': 'Bottom Up',
    'application': 'Application',
    'infrastructure': 'Infrastructure',
    'scale_0_20_m': '$0-$20M',
    'scale_20_50_m': '$20-$50M',
    'scale_50_m_plus': '$50M+',
  }

  function sendUtm(event, field = false) {

    if(field && paramLabels[field]) {
      const label = paramLabels[field];
      const value = paramValues[route.query[field]];

      if (typeof window.dataLayer !== 'undefined') {
        window.dataLayer.push({
          event: 'GrowthEvent',
          eventCategory: 'User Interaction',
          eventAction: 'Parameters Set',
          eventLabel: label,
          eventValue: value,
        });
      }

      // send event to Universal Analytics
      if (typeof window.ga !== 'undefined') {
        window.ga('create', 'UA-45022052-14');
        window.ga('send', {
          hitType: 'event',
          eventCategory: 'GrowthMetrics',
          eventAction: label,
          eventLabel: value,
        });
      }
    }

    // Send Overall Data
    if(route.query.view === 'results') {
      const values = {};
      const eventLabel = event === 'values' ? 'Growth Metrics Parameters Set' : 'Growth Metrics Results Viewed';

      allowedParams.forEach((param) => {
        if (route.query[param]) {
          values[param] = route.query[param];
        }
      });

      if (typeof window.dataLayer !== 'undefined') {
        window.dataLayer.push({
          event: 'GrowthEvent',
          eventCategory: 'User Interaction',
          eventAction: 'Parameters Set',
          eventLabel: eventLabel,
          eventValue: null,
          eventParams: values,
        });
      }

      const valuesString = JSON.stringify(values);
      // console.log(`Growth Metrics Event: ${eventLabel} - ${valuesString}`);

      // send event to Universal Analytics
      if (typeof window.ga !== 'undefined') {
        values.hitType = 'event';
        values.eventCategory = 'GrowthMetrics';
        values.eventAction = eventLabel;
        values.eventLabel = valuesString;
        window.ga('create', 'UA-45022052-14');
        window.ga('send', values);
      }
    }
  }

  return {
    sendUtm,
  };
}

/* eslint-enable */
