<script>
export default {
  name: 'ResultsArticleChartBar',
};
</script>

<script setup>
import ResultsArticleChartBarItem from './ResultsArticleChartBarItem';
import { computed, onMounted, ref } from 'vue';
import Constants from '../../utils/constants';
import Intersection from '../../utils/intersection';
import useChart from "../../composables/useChart.js";

const props = defineProps({
  results: {
    type: Array,
    required: true,
  },
  dataFormat: {
    type: String,
    default: '',
  },
});

const chart = useChart(props.results);
const { computedResult, computedMax, computedMin } = chart;

const elRef = ref(null);

const originalHeight = computed(() => elRef.value ? elRef.value.clientHeight : 0);

const isOnScreen = ref(false);
onMounted(() => {
  const intersection = new Intersection();

  intersection.observe(elRef.value, (entry) => {
    isOnScreen.value = entry.isIntersecting;

    if (isOnScreen.value) {
      intersection.unobserve(elRef.value);
    }
  });
});
</script>

<template>
  <div ref="elRef" class="gm-bar-chart" :class="{ 'is-hidden': !isOnScreen }">
    <results-article-chart-bar-item
      v-if="computedResult"
      color="deep-ocean"
      :percentile="Constants.RESULT.PERCENTILE.TH25"
      :result="computedResult"
      :max="computedMax"
      :min="computedMin"
      :is-on-screen="isOnScreen"
      :original-height="originalHeight"
      :offset="offset"
      :data-format="dataFormat"
    />

    <results-article-chart-bar-item
      v-if="computedResult"
      color="marina"
      :percentile="Constants.RESULT.PERCENTILE.TH50"
      :result="computedResult"
      :max="computedMax"
      :min="computedMin"
      :is-on-screen="isOnScreen"
      :original-height="originalHeight"
      :offset="offset"
      :data-format="dataFormat"
    />

    <results-article-chart-bar-item
      v-if="computedResult"
      color="bayside"
      :percentile="Constants.RESULT.PERCENTILE.TH75"
      :result="computedResult"
      :max="computedMax"
      :min="computedMin"
      :is-on-screen="isOnScreen"
      :original-height="originalHeight"
      :offset="offset"
      :data-format="dataFormat"
    />

    <results-article-chart-bar-item
      v-if="computedResult"
      color="avocado"
      :percentile="Constants.RESULT.PERCENTILE.TH90"
      :result="computedResult"
      :max="computedMax"
      :min="computedMin"
      :is-on-screen="isOnScreen"
      :original-height="originalHeight"
      :offset="offset"
      :data-format="dataFormat"
    />
  </div>
</template>