particleSize;\n  height: $particleSize;\n  border-radius: $particleSize;\n  backface-visibility: hidden;\n  position: absolute;\n  animation-name: move;\n  animation-duration: $animationDuration;\n  animation-timing-function: linear;\n  animation-iteration-count: infinite;\n  $colors: (#0057bf, #93ca15);\n  @for $i from 1 through $amount {\n    &:nth-child(#{$i}) {\n      color: nth($colors, $i % 2 + 1);\n      top: random(100) * 1%;\n      left: random(100) * 1%;\n      animation-duration: (random($animationDuration * 10) / 10) * 1s + 10s;\n      animation-delay: random(($animationDuration + 10s) * 10) / 10 * -1s;\n      transform-origin: (random(50) - 25) * 1vw (random(50) - 25) * 1vh;\n      $blurRadius: (random() + 0.5) * $particleSize * 0.5;\n      $x: if(random() > 0.5, -1, 1);\n      box-shadow: ($particleSize * 2 * $x) 0 $blurRadius currentColor;\n    }\n  }\n}\n\n@keyframes move {\n  100% {\n    transform: translate3d(0, 0, 1px) rotate(360deg);\n  }\n}\n","// @include font-face(\"font-name\", (\"../fonts/font-file-name\"), normal, normal );\n@mixin font-face($font-name, $url, $weight: normal, $style: normal, $type: 'all') {\n    @font-face {\n        font-family: $font-name;\n        font-weight: $weight;\n        font-style: $style;\n\n        @if $type=='all' {\n            font-display: block;\n            src: url('#{$url}.eot?5sv48p');\n            src: url('#{$url}.eot?#iefix5sv48p') format('embedded-opentype'), url('#{$url}.svg?5sv48p') format('svg'), url('#{$url}.ttf?5sv48p') format('truetype'), url('#{$url}.woff?5sv48p') format('woff');\n        }\n\n        @else if $type=='woff2' {\n            src: url('#{$url}.woff2') format('woff2');\n        }\n\n        @else {\n            src: url('#{$url}.woff2') format('woff2'), url('#{$url}.woff') format('woff');\n        }\n    }\n}\n\n$font-path: '/wp-content/themes/a16z-2015/client/fonts';\n\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-black', 900, normal, woff2);\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-black-italic', 900, italic, woff2);\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-bold', 700, normal, woff2);\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-bold-italic', 700, italic, woff2);\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-semibold', 600, normal, woff2);\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-semibold-italic', 600, italic, woff2);\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-medium', 500, normal, woff2);\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-medium-italic', 500, italic, woff2);\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-regular', 400, normal, woff2);\n@include font-face('tiempos', '#{$font-path}/tiempos/tiempos-fine-regular-italic', 400, italic, woff2);\n@include font-face('adellemono', '#{$font-path}/adellemono/adellemono-extrabold', 900, normal, woff2);\n@include font-face('adellemono', '#{$font-path}/adellemono/adellemono-extrabolditalic', 900, italic, woff2);\n@include font-face('adellemono', '#{$font-path}/adellemono/adellemono-bold', 700, normal, woff2);\n@include font-face('adellemono', '#{$font-path}/adellemono/adellemono-bolditalic', 700, italic, woff2);\n@include font-face('adellemono', '#{$font-path}/adellemono/adellemono-semibold', 600, normal, woff2);\n@include font-face('adellemono', '#{$font-path}/adellemono/adellemono-semibolditalic', 600, italic, woff2);\n@include font-face('adellemono', '#{$font-path}/adellemono/adellemono-regular', 400, normal, woff2);\n@include font-face('adellemono', '#{$font-path}/tiempos/adellemono-italic', 400, italic, woff2);\n@include font-face('adellemono', '#{$font-path}/adellemono/adellemono-light', 300, normal, woff2);\n@include font-face('adellemono', '#{$font-path}/tiempos/adellemono-lightitalic', 300, italic, woff2);\n\n$tiempos: 'tiempos', serif;\n$adellemono: 'adellemono', sans-serif;\n","/* SCSS Utilities\n *\n * Helper mixins and functions for use throughout the theme\n */\n\n// Space using rem grid.\n@function spacer($units, $unit: rem) {\n\t@if ($unit == 'em') {\n\t\t@return ($units * 1em);\n\t}\n\n\t@return ( $units * 1rem );\n}\n\n/**\n * Style resets & mixins\n */\n\n// Reset default browser button styles\n@mixin button-reset($color: none) {\n\tbackground: $color;\n\tborder: none;\n\tfont-size: inherit;\n\tpadding: 0;\n}\n\n// Clearfix\n@mixin clearfix {\n\n\t&::after {\n\t\tclear: both;\n\t\tcontent: '';\n\t\tdisplay: table;\n\t}\n}\n\n// Variable Width Full Bleed Mixin\n@mixin full-bleed($width: 100vw) {\n\tmargin-left: 50%;\n\ttransform: translateX(-50%);\n\twidth: #{$width};\n}\n\n// Pins an element to fill a relative container\n@mixin absolute-pinned {\n\tbottom: 0;\n\tleft: 0;\n\tposition: absolute;\n\tright: 0;\n\ttop: 0;\n}\n\n// Hide nested list items\n@mixin hide-submenu {\n\n\tli ul {\n\t\tdisplay: none;\n\t}\n}\n\n// Link hover effect\n@mixin link-hover( $link-color: $steel, $hover-color: $poppy ) {\n\ttext-decoration: none;\n\tcolor: $link-color;\n\n\t&:hover {\n\t\tcolor: $hover-color;\n\t}\n}\n\n// Horizontal Separator\n@mixin separator($character: '|', $spacing: spacer(1)) {\n\n\t&::after {\n\t\tcontent: $character;\n\t\tmargin: 0 $spacing;\n\t}\n\n\t&:last-child {\n\n\t\t&::after {\n\t\t\tdisplay: none;\n\t\t}\n\t}\n}\n\n// Hide element text\n@mixin hide-text {\n\toverflow: hidden;\n\ttext-indent: 100vw;\n\twhite-space: nowrap;\n}\n\n// Hide element text to replace with a background image\n@mixin text-replace($height: 1em, $width: 1em) {\n\t@include hide-text();\n\tbackground-position: center;\n\tbackground-repeat: no-repeat;\n\tdisplay: block;\n\theight: $height;\n\twidth: $width;\n}\n\n// Duplicates necissary styles for a svg mask fallback\n@mixin svg-mask($color, $image) {\n\tbackground-image: url($image);\n\tbackground-position: center;\n\tbackground-repeat: no-repeat;\n\tbackground-size: 100% auto;\n\tcolor: $color;\n\n\t@supports (mask-image: url()) {\n\t\tbackground-color: $color;\n\t\tbackground-image: none;\n\t\tmask-image: url($image);\n\t\tmask-position: center;\n\t\tmask-repeat: no-repeat;\n\t\tmask-size: 100% auto;\n\t}\n}\n\n// Adds vertical spacing to all child elements but the last\n@mixin vertical-rhythym($margin: spacer(1), $padding: 0, $border: none) {\n\n\t> * {\n\t\tborder-bottom: $border;\n\t\tmargin-bottom: $margin;\n\t\tpadding-bottom: $padding;\n\n\t\t&:last-child {\n\t\t\tborder-bottom: none;\n\t\t\tmargin-bottom: 0;\n\t\t\tpadding-bottom: 0;\n\t\t}\n\t}\n}\n\n/**\n * SCSS Dev Utilities\n */\n\n// Highlight an element\n@mixin dev-block($color: red, $background: rgba(255,255,0,0.1), $outline: 1px dotted $color) {\n\toutline: $outline;\n\tbackground: $background;\n}\n\n// objectfit\n%objectfit {\n  position: relative;\n\n  img {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    object-position: center;\n  }\n}\n\n// listreset\n%listreset {\n\tlist-style: none;\n\tpadding: 0;\n\tmargin: 0;\n}\n","/*\n * Typography\n *\n * Theme font families\n * Font family sass function\n */\n\n $font-path: \"/wp-content/themes/a16z-2015/client/fonts\";\n $image-path: \"/wp-content/themes/a16z-2015/client/images\";\n\n// Font Family Stacks\n$font-families: (\n\tsans-serif: \"'proxima-nova', Helvetica, Arial, sans-serif\",\n\tserif: \"Garamond, Baskerville, 'Baskerville Old Face', 'Hoefler Text', 'Times New Roman',  serif\",\n\tlogotype: \"serif\",\n);\n\n$lust: 'lust', serif;\n$din-condensed: 'din-condensed', sans-serif;\n\n// SCSS Fonts Map Shortcut Function\n@function font-family($key) {\n\t@if map-has-key($font-families, $key) {\n\t\t@return unquote(map-get($font-families, $key));\n\t}\n\n\t@warn 'Unknown `#{$key}` in $font-families.';\n\n\t@return null;\n}\n\n// Default Font Declarations\n$font-body: 1rem/1.667 font-family(sans-serif);\n$font-header: 700 1.375rem/1.625rem font-family(sans-serif);\n$font-title: 1rem/1 font-family(logotype);\n\n/* Header Mixins */\n@mixin header() {\n\tfont: $font-header;\n\tletter-spacing: calc(-5 / 1000em);\n\tcolor: $steel;\n}\n\n@mixin section-header() {\n\tborder-bottom: $border-light;\n\tdisplay: flex;\n\tmargin-bottom: rem(24);\n\tpadding-bottom: rem(20);\n}\n\n@mixin section-title() {\n\t@include header();\n\tfont-size: rem(36);\n\tline-height: 1;\n}\n\n@mixin section-subheader() {\n  @include header();\n  font-weight: 500;\n  font-size: rem(28);\n  line-height: 1;\n  color: #9B9B9B;\n}\n\n@mixin page-title() {\n\t@include header();\n\tfont-size: rem(40);\n\tline-height: 1;\n}\n\n@mixin section-subtitle() {\n\tfont-weight: 500;\n\ttext-transform: uppercase;\n}\n\n@mixin post-header() {\n\t@include header();\n\tfont-weight: 500;\n}\n\n@mixin action-link() {\n\t@include link-hover($color-secondary);\n\n\t@include breakpoints(medium) {\n\t\tfont-weight: 500;\n\t}\n}\n\n@mixin screen-reader-text() {\n\tclip: rect(1px, 1px, 1px, 1px);\n\tclip-path: inset(50%);\n\theight: 1px;\n\tmargin: -1px;\n\toverflow: hidden;\n\tpadding: 0;\n\tposition: absolute;\n\twidth: 1px;\n\tword-wrap: normal;\n}\n","/* Theme Breakpoints\n *\n * Media and screen values, and helper functions\n */\n\n$breakpoints: (\n\tx-small: 320px,\n\tmarketomobilebkpt: 480px,\n\tsmall: 640px,\n\ts-medium: 767px,\n\tmedium: 768px,\n\ts-large: 1023px,\n\tlarge: 1024px,\n  sm-large: 1199px,\n  m-large: 1200px,\n\ts-1350: 1350px,\n    s-1366: 1366px,\n\tx-large: 1600px,\n);\n\n// SCSS Breakpoint shortcut mixin\n@mixin breakpoints($from: none, $to: none) {\n\t$min-width: null;\n\t$max-width: null;\n\n\t@if (type-of($from) == string) and (map-has-key($breakpoints, $from)) {\n\t\t$min-width: map-get($breakpoints, $from);\n\t}\n\t@else if ($from == none) {\n\t\t$min-width: false;\n\t}\n\t@else if ((type-of($from) == number) and (unitless($from) == false)) {\n\t\t$min-width: $from;\n\t}\n\t@else {\n\t\t@error 'Min width value #{$from} was not found.';\n\t}\n\n\t@if (type-of($to) == string) and (map-has-key($breakpoints, $to)) {\n\t\t$max-width: map-get($breakpoints, $to);\n\t}\n\t@else if ($to == none) {\n\t\t$max-width: false;\n\t}\n\t@else if ((type-of($to) == number) and (unitless($to) == false)) {\n\t\t$max-width: $to;\n\t}\n\t@else {\n\t\t@error 'Max width value #{$to} was not found.';\n\t}\n\n\t// If both min & max values\n\t@if ($min-width != false) AND ($max-width != false) {\n\t\t$min-width: $min-width + 1px;\n\n\t\t@media (min-width: #{$min-width}) and (max-width: #{$max-width}) {\n\t\t\t@content;\n\t\t}\n\t}\n\t// If either min or max vlaue\n\t@else if ($min-width != false) {\n\t\t$min-width: $min-width + 1px;\n\n\t\t@media (min-width: #{$min-width}) {\n\t\t\t@content;\n\t\t}\n\t}\n\t@else if ($max-width != false) {\n\t\t@media (max-width: #{$max-width}) {\n\t\t\t@content;\n\t\t}\n\t}\n\t// if min and max are both false\n\t@else {\n\t\t@content;\n \t}\n}\n","/* Core CSS Colors\n *\n * List of named colors for use in CSS.\n * SCSS maps `color()` helper function\n */\n\n// Named Project Font Colors\n$colors: (\n\tblack: #0B0B0B,\n\tgray-very-dark: hsl(0,0,20),\n\tgray-dark: hsl(0,0,40),\n\tgray: hsl(0,0,50),\n\tgray-light: hsl(0,0,66),\n\tgray-very-light: hsl(0,0,82),\n\tgray-slate: #40474e,\n\twhite: #FFFFFF,\n  orange: #ED8B00,\n  gray-blue: #7a99ac,\n  gray-slate-blue: #4b4f54,\n\n);\n\n// SCSS Color Map Shortcut Function\n\n@function color($key) {\n  @if map-has-key($colors, $key) {\n    @return map-get($colors, $key);\n  }\n  @warn \"Unknown `#{$key}` in $colors.\";\n\n  @return null;\n}\n\n// Default Project Colors\n$color-primary: color(gray-slate);\n$color-secondary: color(gray-very-light);\n$color-tertiary: color(gray-very-dark);\n$color-highlight: color(orange);\n\n// Books Landing Colors\n$color-beige: #efe9dc;\n$color-orange: #fdb853;\n$color-darktan: #a9903e;\n$color-darkdarktan: #7e6821;\n$color-navy: #042a51;\n$color-grayblue: color(gray-blue);\n\n// Vertical Landing Pages\n$blue-bg: color(gray-blue);\n\n// New Colors\n$black: #000;\n$white: #fff;\n$blue3: #7a99ac;\n$blue4: #dcf3f4;\n$gray: #f8f8f8;\n$gray1: #e5e5e5;\n$gray2: #9d9fa1;\n$gray3: #40474e;\n$gray4: #41474d;\n$gray5: #e7e6e5;\n$gray6: #f5f4f4;\n$gray7: #f9f9f9;\n$gray9: #f6f7f7;\n$purple2: #4d217d;\n\n$steel: #4b5058;\n$concrete: #97999b;\n$fog: #d0d0ce;\n$poppy: #ed8b00;\n$games: #ff6a1b;\n$citrus: #f1c400;\n$avocado: #93c90e;\n$pine: #00873e;\n$marina: #00a3ad;\n$bayside: #5bc2e7;\n$deep_ocean: #0057b8;\n$grapevine: #6e3fa3;\n$cable_car: #ef3340;\n$future_blue: #4100ff;\n","/* Core Layout Values\n *\n * Default theme values\n * Theme layout mixins\n */\n\n/* SCSS Layout Variables */\n\n// Common Layout Values\n$base-size: 16; // Base size in px\n$content-max-width: 40rem;\n$logo-image-ratio: calc(13 / 209 * 100%);\n$site-max-width: rem(1180);\n$footer-line-height: rem(30);\n$three-column-spacer: rem(24);\n\n// Common Visual Styles\n$border-light: 1px solid color(gray-very-light);\n$border-medium: 1px solid color(gray-light);\n$border-dark: 1px solid $steel;\n\n/**\n * SCSS Theme Layout Utilities\n */\n\n// Makes an image frame at a width & height ratio\n@mixin intrinsic-image($width, $height, $vertical-align: middle) {\n  height: 0;\n  overflow: hidden;\n  padding-top: calc((#{$height}/#{$width}) * 100%);\n  position: relative;\n\n  > img,\n  > source {\n    position: absolute;\n\n    @if ($vertical-align == 'top') {\n      top: 0;\n    } @else if ($vertical-align == 'bottom') {\n      bottom: 0;\n    } @else {\n      top: 50%;\n      transform: translateY(-50%);\n    }\n  }\n}\n"]}]);

