<script>
export default {
  name: 'ResultsSidebarQuickLink',
};
</script>

<script setup>
import { gsap } from 'gsap/dist/gsap';
import { ScrollToPlugin } from 'gsap/dist/ScrollToPlugin';
import useBreakPoints from "../../composables/useBreakPoints";

gsap.registerPlugin(ScrollToPlugin);

const props = defineProps({
  item: {
    type: Object,
    required: true,
  },
  classes: {
    type: String,
    default: '',
  },
});

const breakPoints = useBreakPoints();
const { isMobile, isTablet } = breakPoints;

const onClick = (id) => {
  const hash = `#${id}`;
  history.pushState(null, null, hash);
  const target = document.querySelector(hash);

  if (target) {
    if (isMobile.value || isTablet.value) {
      document.body.classList.remove('gm-drawer-active');
      setTimeout(() => {
        gsap.to(window, {
          duration: 0.5,
          scrollTo: {
            y: target,
            offsetY: () => {
              return isMobile.value ? 130 : 180
            }
          },
        });
      }, 100);
    } else {
      gsap.to(window, {
        duration: 0.5,
        scrollTo: {
          y: target,
          offsetY: 100
        },
      });
    }
  }
};
</script>

<template>
  <li :class="classes">
    <a :href="`#${item.id}`" @click.prevent="onClick(item.id)">
      {{ item.title }}
    </a>
  </li>
</template>
