<script>
export default {
  name: 'BackgroundWrapperLights',
};
</script>

<script setup>
import { onMounted, onUnmounted, ref, watch } from 'vue';

import { storeToRefs } from 'pinia';
import { useNavStore } from '../../../stores/nav';
import Constants from '../../../utils/constants';

const props = defineProps({
  id: {
    type: String,
    required: true,
  },

  count: {
    type: Number,
    default: 8,
  },
});

const navStore = useNavStore();
const { view } = storeToRefs(navStore);

const hidden = ref(true);

function hideLights() {
  hidden.value = true;
}

function showLights() {
  hidden.value = false;
}

watch(view, () => {
  if (view.value === Constants.NAV.VIEW.INTERVIEW) {
    hideLights();
  } else if (
    view.value === Constants.NAV.VIEW.INTRO ||
    view.value === Constants.NAV.VIEW.CALCULATING
  ) {
    showLights();
  }
});

onMounted(() => {
  if (
    view.value === Constants.NAV.VIEW.INTRO ||
    view.value === Constants.NAV.VIEW.CALCULATING
  ) {
    showLights();
  }
});

onUnmounted(() => {});
</script>

<template>
  <div :class="{ [`background-${id}`]: true, show: !hidden }">
    <span v-for="i in count" :key="i" />
  </div>
</template>

<style scoped lang="scss">
div {
  opacity: 0.05;
  transition: opacity 2s ease-out;

  &.show {
    opacity: 0.3;
  }
}
</style>
