<script>
export default {
  name: 'ResultsArticleMagicNumber',
};
</script>

<script setup>
import { onMounted, ref } from 'vue';
import Intersection from '../../utils/intersection';

const SYMBOL = '>';
const NUMBER = 0.75;

const numberRef = ref(null);
const currentNumber = ref(0.00);

function easeInOutQuad(t) {
  return t < 0.5 ? 2 * t * t : -1 + (4 - 2 * t) * t;
}

function animateNumber(NUMBER) {
  const start = performance.now();

  requestAnimationFrame(function animate(time) {
    let timeFraction = (time - start) / 2000;

    if (timeFraction > 1) timeFraction = 1;

    const progress = easeInOutQuad(timeFraction);


    currentNumber.value = Math.round(progress * NUMBER * 100) / 100;
    // if not .00, add a decimal
    if (currentNumber.value % 1 !== 0) {
      currentNumber.value = currentNumber.value.toFixed(2);
    }

    if (timeFraction < 1) {
      requestAnimationFrame(animate);
    }
  });
}

const elRef = ref(null);
const isOnScreen = ref(false);
onMounted(() => {
  const intersection = new Intersection();

  intersection.observe(elRef.value, (entry) => {
    isOnScreen.value = entry.isIntersecting;

    if (isOnScreen.value) {
      intersection.unobserve(elRef.value);

      setTimeout(() => {
        animateNumber(NUMBER);
      }, 500);
    }
  });
});
</script>

<template>
  <div ref="elRef" class="magic-number" :class="{ 'is-on-screen': isOnScreen }">
    <span class="symbol">{{ SYMBOL }}</span>
    <span ref="numberRef" class="number">{{ currentNumber }}</span>
  </div>
</template>

<style lang="scss" scoped>
.magic-number {
  font-size: 75px;
  font-weight: 700;
  letter-spacing: -0.5px;
  color: $deep_ocean;
  position: relative;
  padding: 50px 0;
  margin: 0 auto;

  .symbol {
    position: absolute;
    right: calc(50% + 60px);
    padding-right: 10px;
    opacity: 0;
    transform: translate3d(50px, -50%, 0);
    transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
  }

  .number {
    position: absolute;
    right: calc(50% + 60px);
    padding-left: 10px;
    transform: translate3d(100%, -50%, 0);
    opacity: 0;
    transition: opacity 0.5s ease-in-out 0.5s;
  }

  &.is-on-screen {
    .symbol {
      opacity: 1;
      transform: translate3d(0, -50%, 0);
    }

    .number {
      opacity: 1;
    }
  }
}
</style>