<script>
export default {
  name: 'ResultsArticleChartBarItem',
};
</script>

<script setup>
import { computed, onMounted, onUnmounted, ref } from 'vue';
import useChart from '../../composables/useChart.js';

const props = defineProps({
  color: {
    type: String,
    required: true,
  },
  percentile: {
    type: Object,
    required: true,
  },
  result: {
    type: Object,
    required: true,
  },
  max: {
    type: Number,
    required: true,
  },
  min: {
    type: Number,
    required: true,
  },
  isOnScreen: {
    type: Boolean,
    required: true,
  },
  originalHeight: {
    type: Number,
    default: 0,
  },
  dataFormat: {
    type: String,
    default: '',
  },
});

const chart = useChart(props.results);
const { formatNumber } = chart;

const elRef = ref(null);

const number = computed(() => {
  return props.result[props.percentile.value];
});

const formattedNumber = computed(() => {
  return formatNumber(number.value, props.dataFormat);
});

const charsNumber = computed(() => {
  return formattedNumber.value.length;
});

const label = computed(() => {
  return props.percentile.label;
});

const maxHeight = computed(() => {
  if (props.min < 0 && props.max > 0) {
    return props.max - props.min;
  } else if (props.max > 0) {
    return props.max;
  } else {
    return props.min;
  }
});

const heightScale = computed(() => {
  let result = number.value / maxHeight.value;

  if (result < 0) {
    return result * -1;
  }

  return result;
});

const isNegative = computed(() => {
  return number.value < 0;
});

const height = computed(() => {
  return props.originalHeight * heightScale.value;
});

const offset = computed(() => {
  if (props.min < 0 && props.max > 0) {
    if (isNegative.value) {
      return (props.originalHeight * props.max) / (props.max - props.min);
    } else {
      return Math.abs(props.min) * (props.originalHeight / maxHeight.value);
    }
  }

  return 0;
});

const top = computed(() => {
  if (isNegative.value) {
    return height.value - props.originalHeight + offset.value;
  }

  return -offset.value;
});

const infoRef = ref(null);
const infoRefHeight = ref(16);

const isInfoHasTwoLines = computed(() => {
  return infoRefHeight.value > 16;
});

const computedClass = computed(() => {
  const object = { [props.color]: true };

  if (
    heightScale.value < 0.2 ||
    (heightScale.value < 0.3 && isInfoHasTwoLines.value)
  ) {
    object['bar-xs'] = true;
  } else if (heightScale.value < 0.55) {
    object['bar-sm'] = true;
  }

  if (
    props.max <= 0 ||
    ((object['bar-xs'] || object['bar-sm']) &&
      Math.abs(props.min) > props.max) ||
    (number.value < 0 && Math.abs(props.min) > props.max)
  ) {
    object['bar-negative'] = true;
  }

  return object;
});

function onResize() {
  if (infoRef.value) {
    infoRefHeight.value = infoRef.value.offsetHeight;
  }
}

onMounted(() => {
  window.addEventListener('resize', onResize);

  setTimeout(() => {
    onResize();
  }, 100);
});

onUnmounted(() => {
  window.removeEventListener('resize', onResize);
});
</script>

<template>
  <div
    ref="elRef"
    class="bar"
    :class="computedClass"
    :style="{
      height: isOnScreen ? `${heightScale * 100}%` : 0,

      top: `${top}px`,
    }"
  >
    <span
      class="data"
      :class="{ 'push-back': isInfoHasTwoLines, squeeze: charsNumber > 6 }"
    >
      {{ formattedNumber }}
    </span>
    <span ref="infoRef" class="info">{{ label }}</span>
  </div>
</template>