<script>
export default {
  name: 'ResultsArticleChart',
};
</script>

<script setup>
import useChart from "../../composables/useChart.js";
import ResultsArticleChartBar from "./ResultsArticleChartBar.vue";
import ResultsArticleChartInline from "./ResultsArticleChartInline.vue";

const props = defineProps({
  results: {
    type: Array,
    required: true,
  },
  dataFormat: {
    type: String,
    default: "",
  },
});

const chart = useChart(props.results);
const { chartType } = chart;
</script>

<template>
  <results-article-chart-bar
      v-if="chartType === chart.CHAR_TYPE_BAR"
      :results="results"
      :data-format="dataFormat"
  />
  <results-article-chart-inline
      v-else-if="chartType === chart.CHAR_TYPE_INLINE"
      :results="results"
      :data-format="dataFormat"
  />
</template>