import { defineStore, storeToRefs } from 'pinia';
import { computed, watch } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import Constants from '../utils/constants';
import { useNavStore } from './nav';
import useUtm from '../composables/useUtm';

const id = 'growth-app-result';

// eslint-disable-next-line import/prefer-default-export
export const useResultStore = defineStore(id, () => {
  const route = useRoute();
  const router = useRouter();
  const utm = useUtm();

  function cleanValues(array) {
    const query = { ...route.query };
    array.forEach((key) => {
      delete query[key];
    });
    router.push({ query });
  }

  const vertical = computed({
    get: () => route.query.vertical,
    set: (value) => {
      const query = { ...route.query };
      if (null === value) {
        delete query.vertical;
      }
      router.push({ query: { ...query, vertical: value } }).then(() => {
        utm.sendUtm('values', 'vertical');
      });
    },
  });

  const gtmMotion = computed({
    get: () => route.query['gtm-motion'],
    set: (value) => {
      const query = { ...route.query };
      if (null === value) {
        delete query['gtm-motion'];
      }
      router.push({ query: { ...query, 'gtm-motion': value } }).then(() => {
        utm.sendUtm('values', 'gtm-motion');
      });
    },
  });

  const software = computed({
    get: () => route.query.software,
    set: (value) => {
      const query = { ...route.query };
      if (null === value) {
        delete query.software;
      }
      router.push({ query: { ...query, software: value } }).then(() => {
        utm.sendUtm('values', 'software');
      });
    },
  });

  const arrRevenueScale = computed({
    get: () => route.query['arr-revenue-scale'],
    set: (value) => {
      const query = { ...route.query };
      if (null === value) {
        delete query['arr-revenue-scale'];
      }
      router
        .push({ query: { ...query, 'arr-revenue-scale': value } })
        .then(() => {
          utm.sendUtm('values', 'arr-revenue-scale');
        });
    },
  });

  const showSoftware = computed(() => {
    const items = Object.values(Constants.RESULT.SOFTWARE);

    for (let i = 0; i < items.length; i += 1) {
      if (items[i].gtm_motions.includes(gtmMotion.value)) {
        return true;
      }
    }

    return false;
  });

  const metricsData = computed(() => {
    const lines = window.resultsCsvData.split(';');
    const result = [];
    const headers = lines[0].split(',');
    for (let i = 1; i < lines.length; i += 1) {
      const obj = {};
      const currentLine = lines[i].split(',');
      for (let col = 0; col < headers.length; col += 1) {
        obj[headers[col]] = currentLine[col];
      }
      result.push(obj);
    }

    return result;
  });

  const navStore = useNavStore();
  const { view } = storeToRefs(navStore);

  watch(
    () => view.value,
    () => {
      setDefaults();
    }
  );

  setDefaults();

  function setDefaults(force = false) {
    if (view.value === Constants.NAV.VIEW.RESULTS) {
      const object = {};

      if (force || undefined === vertical.value) {
        object.vertical = Constants.RESULT.VERTICAL.ENTERPRISE.value;
      }

      if (force || undefined === gtmMotion.value) {
        object['gtm-motion'] = Constants.RESULT.GTM_MOTION.TOP_DOWN.value;
      }

      if (force || undefined === software.value) {
        object.software = Constants.RESULT.SOFTWARE.APPLICATION.value;
      }

      if (force || undefined === arrRevenueScale.value) {
        object['arr-revenue-scale'] =
          Constants.RESULT.ARR__REVENUE_SCALE.SCALE_0_20_M.value;
      }

      const query = { ...route.query, ...object };
      router.push({ query });
    }
  }

  return {
    vertical,
    arrRevenueScale,
    gtmMotion,
    software,
    showSoftware,
    cleanValues,
    metricsData,

    setDefaults,
  };
});
