import { computed, ref } from 'vue';

export default function useAccordion() {
  const el = ref(null);

  const toggle = () => {
    if ($(el.value).hasClass('active')) {
      $(el.value)
        .removeClass('active')
        .next()
        .slideUp(200);
    } else {
      $(el.value)
        .addClass('active')
        .next()
        .slideToggle(200);
    }
  };

  function init() {
    if ($(el.value).hasClass('active')) {
      $(el.value)
        .next()
        .show();
    } else {
      $(el.value)
        .next()
        .hide();
    }
  }

  const isToggled = computed(() => $(el.value).hasClass('active'));

  return {
    el,
    toggle,
    init,
    isToggled,
  };
}
