<script>
export default {
  name: 'BackgroundWrapper',
};
</script>

<script setup>
import { ref } from 'vue';
import { useNavStore } from '../../stores/nav';
import { storeToRefs } from 'pinia';
import Constants from '../../utils/constants';
import BackgroundWrapperCompass from './BackgroundWrapperCompass';
import BackgroundWrapperCompassMobile from './BackgroundWrapperCompassMobile';
import BackgroundWrapperPattern from './BackgroundWrapperPattern';
import BackgroundWrapperCalculating from './BackgroundWrapperCalculating';
import BackgroundWrapperLightsBottom from './lights/BackgroundWrapperLightsBottom';
import BackgroundWrapperLightsTop from './lights/BackgroundWrapperLightsTop';
import useBreakPoints from "../../composables/useBreakPoints";

const breakPoints = useBreakPoints();
const { isMobile, isTablet, isDesktop } = breakPoints;

const navStore = useNavStore();
const { view } = storeToRefs(navStore);

const bgClass = ref('');

function onCompassUpdate(value) {
  if (value === 'start') {
    bgClass.value = 'bg-green';
  } else if (value === 'half') {
    bgClass.value = 'bg-blue';
  } else if (value === 'end') {
    bgClass.value = null;

    view.value = Constants.NAV.VIEW.RESULTS;
  }
}

</script>

<template>
  <div class="growth-metrics-background" :class="bgClass">
    <background-wrapper-pattern />

    <background-wrapper-lights-bottom />
    <background-wrapper-compass v-if="isDesktop" @update="onCompassUpdate" />
    <background-wrapper-compass-mobile v-else @update="onCompassUpdate" />
    <background-wrapper-lights-top />

    <transition name="calculating">
      <background-wrapper-calculating
        v-if="view === Constants.NAV.VIEW.CALCULATING"
      />
    </transition>
  </div>
</template>

<style>
.calculating-enter-active,
.calculating-leave-active {
  transition: opacity 0.5s;
}
.calculating-enter,
.calculating-leave-to {
  opacity: 0;
}
</style>
