<script>
export default {
  name: 'InterviewItem',
};
</script>

<script setup>
import { computed, onMounted, ref, watch } from 'vue';

const props = defineProps({
  item: {
    type: Object,
    required: true,
  },
  index: {
    type: Number,
    required: true,
  },
  step: {
    type: Number,
    required: true,
  },
  lock: {
    type: Boolean,
    required: true,
  },
});

let timeout = null;

const hideInfo = ref(true);

const emit = defineEmits(['edit']);

const displayTooltip = ref(false);

const editable = computed(() => {
  return props.item.model.value && !props.lock;
});

const hideEdit = computed(() => {
  return props.item.hideEdit;
});

const option = computed(() => {
  for (const argumentsKey in props.item.options) {
    if (props.item.options[argumentsKey].value === props.item.model.value) {
      return props.item.options[argumentsKey];
    }
  }

  return null;
});

const label = computed(() => {
  if (props.item.model.value) {
    const result = option.value.label;

    if (option.value.toLowerCase) {
      return result.toLowerCase();
    }

    return result;
  }

  return null;
});

const tooltip = computed(() => {
  if (props.item.model.value) {
    return option.value.tooltip;
  }

  return null;
});

const info = computed(() => {
  if (props.item.model.value) {
    return option.value.info;
  }

  return null;
});

// const info = computed(() => {
//   if (props.item.model.value) {
//     return option.value.info;
//   }
//
//   return null;
// });

const isSelected = ref(false);
watch(
  () => props.item.model.value,
  (value) => {
    if (timeout) {
      clearTimeout(timeout);
    }

    hideInfo.value = false;

    setTimeout(() => {
      hideInfo.value = true;
    }, 1500);

    if (value) {
      timeout = setTimeout(
        () => {
          isSelected.value = true;
        },
        3000
      );
    } else {
      isSelected.value = false;
    }
  }
);

onMounted(() => {
  isSelected.value = !!props.item.model.value;
});
</script>

<template>
  <h3
    class="vertical"
    :class="{ selected: isSelected && !lock, [item.classes]: item.classes }"
  >
    {{ item.before }}
    <span :class="{ [item.model.value ? 'filled' : 'blank']: true }">
      {{ label }}

      <span v-if="info && item.model.value && step === index + 1" class="pending" :class="{ hide: hideInfo }">
        ({{ info }})
      </span>

      <span v-if="editable && !hideEdit" class="edit">
        <a href="#" @click.prevent="emit('edit', item.cleanValues)">
          edit
        </a>
      </span>

      <span v-if="tooltip && !item.model.value" class="tooltip">
        <i
          class="icon"
          @mouseenter="displayTooltip = true"
          @mouseleave="displayTooltip = false"
        >
          i
        </i>

        <span
          class="tooltip-content"
          :style="{ opacity: displayTooltip ? 1 : 0 }"
        >
          {{ tooltip }}
        </span>
      </span>
    </span>

    <span>
      {{ (!item.noSpace ? ' ' : '') + item.after }}
    </span>
  </h3>
</template>
