import { defineStore } from 'pinia';
import { computed, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import Constants from '../utils/constants';
import useUtm from '../composables/useUtm';

const id = 'growth-app-nav';

// eslint-disable-next-line import/prefer-default-export
export const useNavStore = defineStore(id, () => {
  const route = useRoute();
  const router = useRouter();
  const utm = useUtm();

  const view = computed({
    get: () => route.query.view || Constants.NAV.VIEW.INTRO,
    set: (value) => {
      const query = { ...route.query, view: value };
      if (null === value) {
        delete query.view;
      }
      router.push({ query }).then(() => {
        utm.sendUtm('nav');
      });
    },
  });

  const isResetting = ref(false);

  function reset() {
    isResetting.value = true;

    const array = [
      'view',
      'vertical',
      'gtm-motion',
      'software',
      'arr-revenue-scale',
    ];

    const query = { ...route.query };
    array.forEach((key) => {
      delete query[key];
    });

    router.push({ query }).then(() => {
      window.location.reload();
    });
  }

  return {
    view,
    isResetting,

    reset,
  };
});
