<script>
export default {
  name: 'InterviewButtonsList',
};
</script>

<script setup>
import { ref } from 'vue';

const props = defineProps({
  options: {
    type: Array,
    required: true,
  },
  lock: {
    type: Boolean,
    required: true,
  },
});

const emit = defineEmits(['select']);

function selectOption(option) {
  if ((!option.button || !option.button.disabled) && !props.lock) {
    emit('select', option.value);
  }
}

const tooltip = ref('');
const isTooltipVisible = ref(false);

function showTooltip(option) {
  if (option.button && option.button.tooltip) {
    tooltip.value = option.button.tooltip;
    isTooltipVisible.value = true;
  }
}

const tooltipRef = ref(null);

function moveTooltip(event) {
  if (tooltipRef.value) {
    const parentRect = tooltipRef.value.parentElement.getBoundingClientRect();
    tooltipRef.value.style.left = `${event.clientX - parentRect.left + 15}px`;
    tooltipRef.value.style.top = `${event.clientY - parentRect.top + 20}px`;
  }
}

function hideTooltip() {
  tooltip.value = '';
  isTooltipVisible.value = false;
}
</script>

<template>
  <ul class="btns-list" :class="{ hide: lock }">
    <li v-for="option in options" :key="option.value">
      <a
        href="#"
        class="button"
        :class="{
          [option.button && option.button.disabled
            ? 'is-pending'
            : 'is-hover-green']: true,
        }"
        @click.prevent="selectOption(option)"
        @mouseenter="showTooltip(option)"
        @mousemove="moveTooltip"
        @mouseleave="hideTooltip"
      >
        {{ option.label }}
      </a>
    </li>
  </ul>

  <div
    ref="tooltipRef"
    class="button-tooltip"
    :class="{ show: isTooltipVisible }"
  >
    {{ tooltip }}
  </div>
</template>