<script>
export default {
  name: 'ResultsArticleAdditionalInfo',
};
</script>

<script setup>
import ResultsArticleAdditionalInfoFaq from './ResultsArticleAdditionalInfoFaq';
import useAccordion from '../../composables/useAccordionInverted';
import { computed, onMounted } from 'vue';
import ResultsArticleAdditionalInfoFurtherReading from './ResultsArticleAdditionalInfoFurtherReading';
import ResultsArticleAdditionalInfoConsiderations from './ResultsArticleAdditionalInfoConsiderations';
import useBreakPoints from "../../composables/useBreakPoints";

const props = defineProps({
  item: {
    type: Object,
    required: true,
  },
});

const breakPoints = useBreakPoints();
const { isMobile, isTablet } = breakPoints;

const { el, toggle, init, isToggled } = useAccordion();

const moreOnLabel = computed(() => {
  if (
    props.item.additional_info &&
    props.item.additional_info.more_on__button_label
  ) {
    return props.item.additional_info.more_on__button_label;
  }

  return props.item.title.toLowerCase();
});

const anchor = computed(() => {
  return `#${isToggled.value ? props.item.id : props.item.id + '-info'}`;
});

const offsetTop = computed(() => {
  return isMobile.value ? 50 : 100;
});

const onToggle = (e) => {
  e.preventDefault();

  if (props.item.id) {
    const target = document.querySelector(anchor.value);
    if (target) {
      window.scrollTo({
        top: target.offsetTop + offsetTop.value,
        behavior: 'smooth',
      });
    }
  }

  toggle();
};

onMounted(() => {
  init();
});
</script>

<template>
  <div :id="`${item.id}-info`" class="gm-additional-info">
    <div class="slide">
      <results-article-additional-info-considerations
        v-if="!item.additional_info.considerations.hide"
        :object="item.additional_info.considerations"
      />

      <results-article-additional-info-faq
        v-if="!item.additional_info.faq.hide"
        :items="item.additional_info.faq.items"
      />

      <results-article-additional-info-further-reading
        v-if="!item.additional_info.further_reading.hide"
        :object="item.additional_info.further_reading"
      />
    </div>

    <a ref="el" href="#" data-more @click.stop="onToggle">
      {{ isToggled ? `Collapse ${moreOnLabel}` : `More on ${moreOnLabel}` }}
    </a>
  </div>
</template>