export default class Intersection {
  constructor() {
    this.observer = null;
    this.observed = [];

    this.init();
  }

  init() {
    if (this.observer) {
      return;
    }

    this.observer = new IntersectionObserver(
      this.handleIntersection.bind(this),
      {
        rootMargin: '0px 0px 0px 0px',
        threshold: 1,
      }
    );
  }

  observe(el, callback) {
    this.observer.observe(el);
    this.observed.push({ el, callback });
  }

  unobserve(el) {
    this.observer.unobserve(el);
    this.observed = this.observed.filter((item) => item.el !== el);
  }

  handleIntersection(entries) {
    entries.forEach((entry) => {
      const observed = this.observed.find((item) => item.el === entry.target);
      if (observed) {
        observed.callback(entry);
      }
    });
  }
}
