<script>
export default {
  name: 'ResultsArticle',
};
</script>

<script setup>
import ResultsArticleChart from './ResultsArticleChart';
import ResultsArticleAdditionalInfo from './ResultsArticleAdditionalInfo';
import ResultsArticleFormula from './ResultsArticleFormula';
import { computed } from 'vue';
import { useResultStore } from '../../stores/result';
import { storeToRefs } from 'pinia';
import ResultsArticleMagicNumber from "./ResultsArticleMagicNumber.vue";

const resultStore = useResultStore();
const { metricsData } = storeToRefs(resultStore);

const props = defineProps({
  item: {
    type: Object,
    required: true,
  },
});

const results = computed(() =>
  metricsData.value.filter((element) => {
    return element.metric_id === props.item.id;
  })
);
</script>

<template>
  <article :id="item.id" class="gm-article">
    <div class="text-intro">
      <h3 v-if="item.title">{{ item.title }}</h3>
    </div>

    <results-article-magic-number v-if="item.magic_number" />

    <results-article-chart
      :results="results"
      :data-format="item.data_format"
    />

    <results-article-formula
      v-if="!item.formula.hide"
      :formula="item.formula"
    />

    <results-article-additional-info
      v-if="!item.additional_info.hide"
      :item="item"
    />
  </article>
</template>
