<script>
export default {
  name: 'BackgroundWrapperCompass',
};
</script>

<script setup>
import { useNavStore } from '../../stores/nav';
import { storeToRefs } from 'pinia';
import { onMounted, onUnmounted, ref, watch } from 'vue';
import { gsap } from 'gsap/dist/gsap';
import Constants from '../../utils/constants';

const navStore = useNavStore();
const { view } = storeToRefs(navStore);

const emit = defineEmits(['update']);
const isHidden = ref(true);

const itemClasses = [
  '.compass-center',
  '.compass-ring-a',
  '.compass-ring-b',
  '.compass-ring-c',
  '.compass-numbers',
];

let interval;

function rotateRingInfinity(itemClass, direction) {
  const SPEED_MAX = 20;
  const SPEED_MIN = 15;

  const speed = Math.random() * (SPEED_MAX - SPEED_MIN) + SPEED_MIN;
  const distance = 360;

  gsap.to(itemClass, {
    rotation: direction === 'left' ? `+= ${distance}` : `-= ${distance}`,
    duration: speed,
    ease: 'none',
    repeat: -1,
  });
}

function rotateRingRandomly(itemClass, direction) {
  const SPEED_MAX = 5;
  const SPEED_MIN = 2.5;

  const DISTANCE_MAX = 90;
  const DISTANCE_MIN = 180;

  const speed = Math.random() * (SPEED_MAX - SPEED_MIN) + SPEED_MIN;

  const currentRotation = gsap.getProperty(itemClass, 'rotation');
  const distance = Math.random() * (DISTANCE_MAX - DISTANCE_MIN) + DISTANCE_MIN;

  gsap.fromTo(
    itemClass,
    {
      rotation: currentRotation,
    },
    {
      rotation: direction === 'left' ? `+= ${distance}` : `-= ${distance}`,
      duration: speed,
      ease: 'power4.inOut',
      onComplete: () => {
        if (view.value === Constants.NAV.VIEW.INTRO) {
          rotateRingRandomly(
            itemClass,
            direction === 'left' ? 'right' : 'left'
          );
        }
      },
    }
  );
}

function displayCompass() {
  if (isHidden.value) {
    itemClasses.forEach((itemClass, index) => {
      gsap.fromTo(
        itemClass,
        {
          opacity: 0,
          scale: 0.8,
        },
        {
          opacity: 1,
          scale: 1,
          duration: 1,
          ease: 'power4.inOut',
          delay: index * 0.1,
        }
      );
    });

    isHidden.value = false;
  }
}

function hideCompass() {
  if (!isHidden.value) {
    itemClasses.forEach((itemClass, index) => {
      gsap.to(itemClass, {
        opacity: 0,
        scale: 0.8,
        duration: 1,
        ease: 'power4.inOut',
        delay: index * 0.1,
      });
    });

    isHidden.value = true;
  }
}

function randomNoise() {
  const array = [];
  const element = document.querySelector('.compass');
  ['.compass-ring-a', '.compass-ring-b'].forEach((itemClass) => {
    const group = element.querySelector(itemClass);
    const randomElement =
      group.children[Math.floor(Math.random() * group.children.length)];
    array.push(randomElement);
  });

  array.forEach((item) => {
    gsap.to(item, {
      opacity: 0.1,
      duration: 0.01,
      ease: 'power4.inOut',
      yoyo: true,
      repeat: getRandomRepeat(),
      delay: Math.random() * 10,
    });
  });
}

function getRandomRepeat() {
  let result = Math.floor(Math.random() * 30) + 30;
  return result % 2 === 0 ? result + 1 : result;
}

function spin() {
  emit('update', 'start');
  let isHalfway = false;

  const timeLine = gsap.timeline({
    onComplete: () => {
      emit('update', 'end');
    },

    onUpdate: () => {
      if (timeLine.progress() > 0.5 && !isHalfway) {
        isHalfway = true;
        emit('update', 'half');
      }
    },
  });

  itemClasses.forEach((itemClass, index) => {
    timeLine.to(
      itemClass,
      {
        rotation: '+=540',
        duration: 3,
        ease: 'power4.inOut',
      },
      index * 0.2
    );
  });
}

function rotateRingsInfinity() {
  itemClasses.forEach((itemClass, index) => {
    rotateRingInfinity(itemClass, index % 2 === 0 ? 'left' : 'right');
  });
}

function rotateRingsRandomly() {
  itemClasses.forEach((itemClass, index) => {
    rotateRingRandomly(itemClass, index % 2 === 0 ? 'left' : 'right');
  });
}

function rotate() {
  if (window.introCompassAnimation === 'infinity') {
    rotateRingsInfinity();
  } else {
    rotateRingsRandomly();
  }
}

watch(view, () => {
  if (view.value === Constants.NAV.VIEW.INTERVIEW) {
    hideCompass();
  } else if (
    view.value === Constants.NAV.VIEW.INTRO ||
    view.value === Constants.NAV.VIEW.CALCULATING
  ) {
    killTweens();
    displayCompass();

    if (view.value === Constants.NAV.VIEW.INTRO) {
      rotate();
    } else if (view.value === Constants.NAV.VIEW.CALCULATING) {
      spin();
    }
  }
});

onMounted(() => {
  itemClasses.forEach((itemClass, index) => {
    gsap.set(itemClass, {
      transformOrigin: '50% 50%',
    });
  });

  if (
    view.value === Constants.NAV.VIEW.INTRO ||
    view.value === Constants.NAV.VIEW.CALCULATING
  ) {
    displayCompass();

    if (view.value === Constants.NAV.VIEW.INTRO) {
      rotate();
    } else if (view.value === Constants.NAV.VIEW.CALCULATING) {
      spin();
    }
  }

  interval = setInterval(randomNoise, 2000);
  randomNoise();
});

function killTweens() {
  itemClasses.forEach((itemClass) => {
    gsap.killTweensOf(itemClass);
  });
}

onUnmounted(() => {
  killTweens();
  clearInterval(interval);
});
</script>

