import { ref } from 'vue';

export default function useAccordionInverted() {
  const el = ref(null);
  const isToggled = ref(false);

  const toggle = () => {
    if ($(el.value).hasClass('active')) {
      isToggled.value = false;

      $(el.value)
        .removeClass('active')
        .prev()
        .slideUp(200);
    } else {
      isToggled.value = true;

      $(el.value)
        .addClass('active')
        .prev()
        .slideToggle(200);
    }
  };

  function init() {
    if ($(el.value).hasClass('active')) {
      isToggled.value = true;

      $(el.value)
        .prev()
        .show();
    } else {
      isToggled.value = false;

      $(el.value)
        .prev()
        .hide();
    }
  }

  return {
    el,
    toggle,
    init,
    isToggled,
  };
}
